import React, { useEffect } from 'react';
import '../../styles/_sandbox.scss';

import { useMediaDevices } from 'react-use';
import { hasMediaRecorder } from '../../services/hasMediaRecorder';
import { isSafari, isIos } from '../../services/userAgent';

const Sandbox = props => {
  const state = useMediaDevices();

  return (
    <React.Fragment>
      <table>
        <tr><td>Is iOS </td><td>{isIos().toString()}</td></tr>
        <tr><td>Is Safari </td><td>{isSafari().toString()}</td></tr>
        <tr><td>Has MediaRecorder </td><td>{hasMediaRecorder().toString()}</td></tr>
      </table>
      <pre>
        {JSON.stringify(state, null, 2)}
      </pre>
    </React.Fragment>
  )
}

export default Sandbox;